.button {
  cursor: pointer;
  width: 100%;
  color: #fff;
  text-align: center;
  background-color: #3369f3;
  border: none;
  border-radius: 8px;
  margin-bottom: 14px;
  padding: 12px 0;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
  transition: opacity .3s;
}

.button:hover {
  opacity: .8;
}

.button_disable {
  opacity: .8;
  cursor: default;
}

